@font-face {
  font-family: 'garagebin';
  src: url('../font/garagebin.eot?36564196');
  src: url('../font/garagebin.eot?36564196#iefix') format('embedded-opentype'),
       url('../font/garagebin.woff2?36564196') format('woff2'),
       url('../font/garagebin.woff?36564196') format('woff'),
       url('../font/garagebin.ttf?36564196') format('truetype'),
       url('../font/garagebin.svg?36564196#garagebin') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
